<template>
  <div class="Deals">
    <div class="container">
      <section>
        <banner></banner>
      </section>
      <section>
        <div class="section-hdr">
          <h3>Special Offers</h3>
        </div>

        <div class="row">
          <div
            class="col-6 col-md-4 col-lg-3 mt-3"
            v-for="(product, index) in products"
            :key="index"
          >
            <card-a :product="product"></card-a>
          </div>
          
        </div>
      </section>
    </div>
        <pagination :pages="pages" @navigate="getCategoryProducts($event)" />
  </div>
</template>

<script>
import Banner from "../components/banner/Banner.vue";
import CardA from "../components/cards/CardA.vue";
import Pagination from "../components/common/Pagination.vue";
export default {
  components: { CardA, Banner, Pagination },
  name: "Deals",
  data: function() {
    return {
      products: [],
      pages: Number,
    };
  },
  methods: {
    getCategoryProducts(page=1) {
      this.$store
        .dispatch("categoryProducts", {
          category_id: this.categoryId,
          page_number:page
        })
        .then((res) => {
          this.pages = 5;
          if (res == undefined) {
            this.products = [];
            return;
          }
          this.products = res.data;
          this.products = this.products.filter((product) => {
            return product.price_old > 0;
          });
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.$store.commit("stopLoading");
        });
    },
    filterd() {},
  },
  computed: {},
  created() {
    this.$store.commit("startLoading");
    this.getCategoryProducts();
  },
  mounted() {},
};
</script>

