<template>
  <transition name="slide-left">
    <div class="FilterMobile" v-show="left">
      <div class="close">
        <i class="fas fa-times" @click="close"></i>
      </div>

      <div class="nav-links mt-5">
        <ul class="d-flex flex-column w-100 py-2 m-0 list-unstyled">
          <li class="w-100 ">
            <p @click="ToogleALlCat" class="px-4">All Categories <i class="fas fa-chevron-down"></i></p>
            <ul
              class="w-100 list-unstyled m-0 position-relative mydropdown ps-2"
              :class="{ active: AllCategories }"
            >
              <li
                class="sec-li"
                v-for="(cat, index) in categories"
                :key="index"
              >
                <p class="hover" v-html="cat.name"></p>
                <ul class="list-unstyled m-0 ul-abs ">
                  <li
                    class="py-2 ps-4"
                    v-for="(subcat, k) in cat.related_categories"
                    :key="k"
                  >
                    <router-link
                      :to="{ name: 'Category', params: { id: subcat.term_id } }"
                      class="routerLink d-block"
                    >
                      <span
                        v-html="subcat.name"
                        @click="close"
                        class="d-block"
                      ></span>
                    </router-link>
                  </li>
                </ul>
              </li>
            </ul>
          </li>
          <li class="w-100" @click="close">
            <router-link to="/Deals" class="routerLink1 px-4 d-block"
              >Daily Deals</router-link
            >
          </li>
          <li class="w-100" @click="close">
            <router-link to="/Market" class="routerLink1 px-4 d-block"
              >Supper Market</router-link
            >
          </li>
          <li class="w-100" @click="close">
            <a href="#" class="routerLink1 d-block px-4">English</a>
          </li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script>
import CustomSlider from "vue-custom-range-slider";
import "vue-custom-range-slider/dist/vue-custom-range-slider.css";
export default {
  components: { CustomSlider },
  props: ["left", "searchKeyword", "relatedCat"],

  data() {
    return {
      search: "",
      radio: [],
      AllCategories: false,
      priceRange: "100",
    };
  },
  methods: {
    close() {
      this.$emit("closeSidefilter");
    },
    searching() {
      this.$router.push({
        name: "Category",
        params: { id: this.categoryId },
        query: { search: this.search },
      });
    },
    ToogleALlCat() {
      this.AllCategories = !this.AllCategories;
    },
  },
  computed: {
    categories() {
      return this.$store.state.categories;
    },
  },
  created() {},
};
</script>

<style lang="scss" scoped>
* {
  box-sizing: border-box;
  margin: 0;
}
.routerLink1{
    text-decoration: none;
    color: #2c3e50;
    padding: 8px 16px;
}
.FilterMobile {
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  height: 100vh;

  background-color: #fff;
  z-index: 1000;
  box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
}

.close {
  position: relative;
  i {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 20px;
    cursor: pointer;
  }
}
.slide-left-enter-active,
.slide-left-leave-active {
  transition: left 0.3s ease;
}
.slide-left-enter {
  left: -300px;
}
.slide-left-enter-to {
  left: 0;
}
.slide-left-leave-to {
  left: -300px;
}
.routerLink {
  text-decoration: none;
  color: #727272;
  font-weight: 500;
  cursor: pointer;
  &.router-link-exact-active {
    color: #000;
    font-weight: bold;
  }
}

.mydropdown {
  display: none;
  width: 100%;
  opacity: 0;
  transition: 0.3s ease;
  &.active {
    opacity: 1;
    display: block;
    background-color: #ff4136;
  }
}
.sec-li {
  &:hover > p {
    background-color: rgb(182, 182, 182);
  }
  &:hover > ul {
    height: auto;
  }
  background-color: rgb(228, 228, 228);
}
.ul-abs {
  position: relative;
  background-color: #fff;
  height: 0;
  overflow: hidden;
  transition: all 0.3s ease;

  &:hover {
    height: auto;
  }
  li {
    transition: all 0.3s ease;
    &:hover {
      background-color: #ff4136;
      color: #fff;
    }
    &:hover > .routerLink {
      color: #fff;
    }
  }
}
ul > li {
  transition: all 0.3s ease;
  &:hover > p {
    color: #fff;
    background-color: #ff4136;
  }
}
ul > li > p {
  padding: 8px 16px;
}
.hover {
  &:hover + .ul-abs {
    height: auto;
  }
}
</style>
