  methods:{
    async getCategories() {
      if(this.$store.state.relatedCategories.length<=0){

        this.$store.commit("startLoading");
        await this.$store.dispatch("getCategories2")
      }else{
        this.$store.commit("startLoading");
        this.categories=this.$store.state.relatedCategories.slice(0,12);
      }
    }
  },
  computed: {
    loading() {
      return this.$store.state.loading;
    },
  },
  created() {
    this.getCategories();
  },
};
</script>

