<template>
  <div class="Pagination mt-5" v-if="pages > 1">
    <div class="d-flex flex-wrap justify-content-center align-items-center">
      <div v-for="(page, index) in pages" :key="index">
        <button class="pagination-button" :class="{active:currentPage==page}" @click="getCategoryProducts(page)">
          {{ page }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name:"Pagination",
    props:["pages","currentPage"],
    methods:{
        getCategoryProducts(page){
            this.$emit("navigate",page)
        },
    }
};
</script>

<style lang="scss" scoped>
.Pagination {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  .pagination-button {
    background: transparent;
    border: 1px solid #c0c0c0;
    padding: 5px 12px;
    font-size: 18px;
    color: red;
    transition: 0.3s ease;
    &:hover {
      background-color: #f0f0f0;
    }
    &.active{
      background-color: #f00;
      color: #fff;
      &:hover{
        background-color: rgb(194, 2, 18);
      }
    }
  }
}
</style>
