<script>
import carousel from "vue-owl-carousel";
import ProdCardComponent from "./ProdCardComponent.vue";
import axios from "axios";
export default {
  components: { ProdCardComponent, carousel },
  props: ["category"],
  data() {
    return {
      products: [],
      
    };
  },
  methods: {

    getCategoryProducts(payload){
      this.$store.dispatch("categoryProducts",{category_id :payload})
      .then((res)=>{
        if(res.data==undefined){
          this.products=[];
          return;
        }
        this.products=res.data;
      }).catch((err)=>{
        console.log(err);
      }).finally(()=>{
        setTimeout(()=>{
          this.$store.commit("stopLoading");
        },500)
      })
    }
    
  },
  computed:{
    checkProducts(){
      if(this.products.length>0){
        return true;
      }
      return false
    }
  },
  created(){
    this.getCategoryProducts(this.category.term_id);

  },
  mounted() {
  },
};
</script>

