<template>
  <div class="aa-navbar-top">
    <div class="container">
      <div class="d-flex align-items-center justify-content-between">
        <router-link to="/"
          ><img src="../../assets/Logo-1.png" alt="logo"
        /></router-link>
        <div class="d-none d-md-block">
          <!-- <button class="aa-login-btn">Login / Register</button> -->
          <HeaderBottom />
        </div>
        <div class="icon d-md-none d-block">
          <i class="fa fa-bars" @click="openFilterSide"></i>
        </div>
        
      </div>
    </div>
    <SideBar
          :left="filterLeft"
          @closeSidefilter="toggleSidefilter"
        />
        <transition name="fade">
          <div
            class="overlay"
            v-show="filterLeft"
            @click="toggleSidefilter"
          ></div>
        </transition>
  </div>
</template>

<script>
import HeaderBottom from "@/components/common/HeaderBottom.vue";
import SideBar from "@/components/common/sidebar.vue";
export default {
  name: "HeaderComponent",
  components: { HeaderBottom , SideBar},
  data: function() {
    return {
      filterLeft: false,
    };
  },
  methods: {
    toggleSidefilter() {
      this.filterLeft = false;
    },
    openFilterSide() {
      this.filterLeft = true;
    },
    checkScreenWidth() {
      const windowWidth = window.innerWidth;
      if (windowWidth <= 750) {
        this.filterLeft = true;
        return;
      }
      this.filterLeft = false;
    },
  },
  created(){
        window.addEventListener("resize", this.checkScreenWidth);

  }
};
</script>

<style scoped>
.aa-navbar-top {
  align-items: center;
  padding: 1rem;
  background-color: #000;
}

.aa-navbar-top .aa-login-btn {
  padding: 8px 16px;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  outline: none;
  text-decoration: none;
  transition: all 0.4s ease 0s;
  font-weight: 700;
  white-space: normal;
  position: relative;
  font-style: normal;
  border: 1px solid transparent;
  background-color: #f6f6f6;
  color: #111;
  line-height: 15px;
  font-size: 15px;
  text-shadow: none;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 2px rgba(0, 0, 0, 0.08);
}

.aa-navbar-top .aa-login-btn:hover {
  background-color: #b1b1b1;
}
.icon i {
  color: #fff;
  cursor: pointer;
  font-size: 20px;
}
.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 999;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-to {
  opacity: 1;
}
.fade-leave-to {
  opacity: 0;
}
</style>
