<template>
  <div class="Category">
    <div class="container">
      <div class="row">
        <BrandFilterMobile
          :searchKeyword="categoryTitle"
          :left="filterLeft"
          :relatedCat="ralatedCategories"
          @closeSidefilter="toggleSidefilter"
        />
        <transition name="fade">
          <div
            class="overlay"
            v-show="filterLeft"
            @click="toggleSidefilter"
          ></div>
        </transition>

        <div class="d-none d-lg-block col-4">
          <brand-filter
            :searchKeyword="categoryTitle"
            :categoryId="categoryId"
            :relatedCat="ralatedCategories"
          ></brand-filter>
        </div>
        <div class="col-12 col-lg-8">
          <div class="products" v-if="products.length > 0">
            <ProductsLoading v-if="productsLoading" />
            <div
              class="h1 my-4"
              v-if="products.length > 0"
              v-html="this.categoryTitle"
            ></div>
            <div class="row">
              <div class="offset-9 col-3 d-lg-none">
                <button class="showFilter" @click="openFilterSide">
                  Filters
                </button>
              </div>
              <div
                class="col-12 col-md-4 col-lg-6 mt-2 col-xl-4"
                v-for="(product, index) in products"
                :key="index"
              >
                <card-a v-if="products.length > 0" :product="product"></card-a>
              </div>
            </div>
            <Pagination
              :pages="pages"
              :currentPage="currentPage"
              @navigate="getCategoryProducts($event)"
            />
          </div>
          <div class="alert alert-warning mt-4" v-else>
            <p class="m-0">No Products with Name {{ this.categoryTitle }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardA from "../components/cards/CardA.vue";
import BrandFilter from "../components/filter/BrandFilter.vue";
import BrandFilterMobile from "@/components/filter/BrandmobileFilter";
import ProductsLoading from "../components/common/productsLoading.vue";
import Pagination from "@/components/common/Pagination";
export default {
  components: {
    BrandFilter,
    CardA,
    BrandFilterMobile,
    ProductsLoading,
    Pagination,
  },
  data() {
    return {
      categoryId: "",
      currentCat: null,
      products: [],
      search: "",
      ralatedCategories: [],
      categoryTitle: "",
      filterLeft: false,
      searchedData: {},
      pages: Number,
      currentPage: 1,
    };
  },
  methods: {
    checkScreenWidth() {
      const windowWidth = window.innerWidth;
      if (windowWidth <= 750) {
        this.filterLeft = true;
        return;
      }
      this.filterLeft = false;
    },

    toggleSidefilter() {
      this.filterLeft = false;
    },
    openFilterSide() {
      this.filterLeft = true;
    },
    updateRoute() {
      if (this.$route.name == "Category") {
        this.categoryId = this.$route.params.id ? this.$route.params.id : null;
        this.search = "";
        if (this.$route.query.search) {
          this.search = this.$route.query.search;
        }
      } else {
        this.search = this.$route.query.search;
        
      }
      this.getCategoryProducts();
    },
    // related categories
    
    getSingleCat() {
      this.$store
        .dispatch("singleCategory", this.categoryId)
        .then((res) => {
          this.currentCat = res;
          this.ralatedCategories = this.$store.state.categories[
            this.currentCat.parent
          ].related_categories;
        })
        .catch((err) => {
          console.error(err);
        })
        .finally(() => {
          this.categoryTitleSet();
        });
    },
    // set Title
    categoryTitleSet() {
      if (this.$route.name == "Category") {
        if (!this.search) {
          this.categoryTitle = `${this.currentCat.name} Category`;
          return;
        }
        this.categoryTitle = `You Searched For "${this.search}"`;
      } else {
        this.categoryTitle = `You Searched For "${this.search}"`;
      }
    },
    // get products
    getCategoryProducts(page = 1) {
      this.$store.commit("startProductsLoading");
      this.currentPage = page;
      // getting request params ready
      if (this.$route.name == "Category") {
        if (!this.search) {
          this.searchedData = {
            category_id: this.categoryId,
            page_number: page,
          };
        } else {
          this.searchedData = {
            category_id: this.categoryId,
            page_number: page,
            search: this.search,
          };
        }
      } else {
        if (this.search) {
          this.searchedData = {
            search: this.search,
            page_number: page,
          };
        } else {
          this.searchedData = {
            page_number: page,
          };
        }
      }
      // send the requests

      // get current category info to get related categories
      this.getSingleCat();

      // get cuurent category products
      this.$store
        .dispatch("categoryProducts", this.searchedData)
        .then((res) => {
          // 
          this.pages = 2;
          if (res.data == undefined) {
            this.products = [];
          } else {
            this.products = res.data;
          }
        })
        .catch((err) => {
          console.error(err);
        })
        .finally(() => {
          this.$store.commit("stopLoading");
          this.$store.commit("stopProductsLoading");
        });
    },
  },
  computed: {
    productsLoading() {
      return this.$store.state.productsLoading;
    },
  },
  watch: {
    $route: "updateRoute",
  },
  async created() {
    window.addEventListener("resize", this.checkScreenWidth);

    await this.$store.commit("startLoading");
    await this.updateRoute();
  },
};
</script>

