import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import "core-js/es/array";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    categories: [],
    relatedCategories: [],
    loading: false,
    paginateProducts: [],
    productsLoading: false,
  },
  getters: {},
  mutations: {
    stopLoading(state) {
      state.loading = false;
    },
    startLoading(state) {
      state.loading = true;
    },
    stopProductsLoading(state) {
      state.productsLoading = false;
    },
    startProductsLoading(state) {
      state.productsLoading = true;
    },

    setCategories(state, payload) {
      // state.categories = payload;
      state.categories = payload;
      for (const k in payload) {
        state.relatedCategories.push(payload[k].related_categories[0]);
      }
    },
  },
  actions: {
    // categories
    // getCategories({ commit, state }) {
    //   return new Promise((resolve, reject) => {
    //     axios
    //       .get("https://owniqs.com/api/v1/categories", {
    //         headers: {
    //           "API-KEY": "JHSCWphcFyDyJ4Fp",
    //         },
    //       })
    //       .then((res) => {
    //         commit("setCategories", res.data.data);
    //         resolve(res.data.data);
    //       })
    //       .catch((err) => {
    //         reject(err);
    //       });
    //   });
    // },
    // categories2
    getCategories2({ commit, state }) {
      axios
        .get("https://owniqs.com/api/v1/categories2", {
          headers: {
            "API-KEY": "JHSCWphcFyDyJ4Fp",
          },
        })
        .then(async (res) => {
          const data = await res.data.data;

          // console.log(data)
          commit("setCategories", data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // get single category
    singleCategory({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios.get("https://owniqs.com/api/v1/categories2", {
          params: {
            category_id: payload,
          },
          headers: {
            "API-KEY": "JHSCWphcFyDyJ4Fp",
          },
        }).then((res)=>{
          resolve(res.data.data)
        }).catch((err)=>{
          reject(err)
        })
      });
    },
    // single category products
    categoryProducts({ commit, state }, payload) {
      return new Promise((resolve, reject) => {
        axios
          .get("https://owniqs.com/api/v1/products2", {
            params: payload,
            headers: {
              "API-KEY": "JHSCWphcFyDyJ4Fp",
            },
          })
          .then((res) => {
            // state.paginateProducts=res.data.data.data;
            resolve(res.data.data);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // single product
    singleProduct({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios
          .get("https://owniqs.com/api/v1/products2", {
            params: {
              post_id: payload,
            },
            headers: {
              "API-KEY": "JHSCWphcFyDyJ4Fp",
            },
          })
          .then((res) => {
            resolve(res.data.data);
          })
          .catch((err) => {
            console.log(err);
            reject(err);
          });
      });
    },
  },
  modules: {},
});
